"use client";
import React, { useEffect, useState } from "react";
import SearchIC from "@/assets/svg/search";
import MoreIC from "@/assets/svg/more";
import CancelIC from "@/assets/svg/cancel";
import NavMenu from "./NavMenu";
import SearchBar from "./Search/SearchBar";
import {
  HeaderWrapper,
  MobileMenu,
  NavOverlay,
  ActionArea,
} from "./Header.styles";

interface CateProps {
  id: number;
  tile: string;
  documentId: string;
}

const Header = () => {
  const [isSearch, setIsSearch] = useState(false);
  const [isNavOpen, setIsNavOpen] = useState(false);
  const [cate, setCate] = useState<CateProps[]>([]);

  const handleGetCate = async () => {
    try {
      const response = await fetch(
        "http://localhost:1337/api/cates?populate=*"
      );
      const data = await response.json();
      setCate(data.data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    handleGetCate();
  }, []);

  if (!cate.length) return null;

  return (
    <HeaderWrapper>
      {!isSearch ? (
        <>
          {/* Mobile menu button */}
          <MobileMenu>
            <button onClick={() => setIsNavOpen(true)}>
              <MoreIC />
            </button>
            <button onClick={() => setIsSearch(true)}>
              <SearchIC />
            </button>
          </MobileMenu>

          {/* Overlay mobile menu */}
          {isNavOpen && (
            <NavOverlay>
              <ActionArea>
                <button onClick={() => setIsNavOpen(false)}>
                  <CancelIC />
                </button>
              </ActionArea>
              <NavMenu
                cate={cate}
                isVertical
                onCloseOverlay={() => setIsNavOpen(false)}
              />
            </NavOverlay>
          )}

          {/* Desktop menu */}
          <NavMenu cate={cate} />
        </>
      ) : (
        <SearchBar onCancel={() => setIsSearch(false)} />
      )}
    </HeaderWrapper>
  );
};

export default Header;
